import d from "./users.module.css";
import Paginator from "../commons/helpers/paginator/Paginator.jsx";
import User from "./User/User";
const Users = ({totalUsersCount, pageSize, onPageChanged, currentPage, usersInfo,
isFollow, isAuth, unfollow, follow }) =>{
    return (
          <div className={d.wrapper}>
            <div className={d.users}>
            <Paginator totalItemsCount = {totalUsersCount} pageSize = {pageSize}
             onPageChanged = {onPageChanged} currentPage = {currentPage}/>
            {usersInfo.map((u)=>{
            return <User user={u} isFollow = {isFollow} isAuth={isAuth} 
             unfollow = {unfollow} follow={follow} photos = {usersInfo.photos}/>
           })}
            </div>
          </div>
        )}
export default Users