import { createSelector } from 'reselect';
const getUsersInfo = (state) =>{
    return state.usersPage.users
}
export const getUsersInformation = createSelector(getUsersInfo, (users)=>{
    console.log('users was getted')
    return users.filter((u=>true))
})
export const getTotalUsersCount = (state) =>{
    return  state.usersPage.totalUsersCount;
}
export const getPageSize = (state) =>{
    return state.usersPage.pageSize;
}
export const getCurrentPage = (state) =>{
    return state.usersPage.currentPage;
}
export const getPreloadingValue = (state) =>{
    return state.usersPage.isPreloading;
}
export const getFollowValue = (state) =>{
    return state.usersPage.isFollowProgressing
}
export const getAuthValue = (state) => {
    return state.auth.isAuth
}

