import React from "react";
import { getUsersInformation,getTotalUsersCount, getPageSize, getCurrentPage,
  getPreloadingValue, getFollowValue, getAuthValue } from "../../redux/selectors";
import { connect} from "react-redux";
import {followingInProgress,
setCurrentPage, getUsers, follow,unfollow,} from '../../redux/users-reduser';
import Preloader from "../commons/Preloader";
import Users from "./Users";
class UsersAPIComponent extends React.Component {
  componentDidMount = () => {
    const {getUsers, currentPage, totalUsersCount} = this.props
     getUsers(currentPage, totalUsersCount)
    }
  pageOnChanged = (pageNumber) => {
    const {getUsers, setCurrentPage, totalUsersCount} = this.props
    setCurrentPage(pageNumber)
    getUsers(pageNumber, totalUsersCount)
    }
  render() {
   
    return (
      <>
      {this.props.isPreloading ? <Preloader /> : <p></p>}
       <Users
        onPageChanged={this.pageOnChanged}
        currentPage={this.props.currentPage}
        usersInfo={this.props.usersInfo}
        pageSize={this.props.pageSize}
        totalUsersCount={this.props.totalUsersCount}
        isFollow={this.props.isFollow}
        follow ={this.props.follow}
        unfollow ={this.props.unfollow}
        isAuth = { this.props.isAuth}
        />     
      </>);}}
const mapStateToProps = (state) => {
  return{
  usersInfo: getUsersInformation(state),
  totalUsersCount: getTotalUsersCount(state),
  pageSize: getPageSize(state),
  currentPage: getCurrentPage(state),
  isPreloading: getPreloadingValue(state),
  isFollow: getFollowValue(state),
  isAuth: getAuthValue(state),
}}
const UsersContainer = connect(mapStateToProps,{setCurrentPage,
  getUsers,followingInProgress, follow,unfollow,})(UsersAPIComponent)
export default UsersContainer