import s from "./Paginator.module.css";
import { useState } from "react";
const Paginator = ({totalItemsCount, pageSize, onPageChanged, currentPage, portionSize = 10}) =>{
    const pagesCount = (totalItemsCount / pageSize);
   const pagesNumbers =[];
   for(let i = 1; i<=pagesCount; i++){
     pagesNumbers.push(i)}
    let portionCount = pagesCount / portionSize
    let [portionNumber, setPortionNumber] = useState(1)
    let leftPortionPageNumber = (portionNumber - 1) * portionSize + 1
    let rightPortionPageNumber = portionNumber * portionSize
    return <div className={s.pageNumContainer}>
        {portionNumber > 1 && <button button = 'prev' onClick={()=>{setPortionNumber(portionNumber -1)}}>Prev</button>}
           {pagesNumbers.filter(p=>p >= leftPortionPageNumber && p<=rightPortionPageNumber)
           .map(p=>{
            return <span className={p === currentPage && s.selectedPage} 
            onClick = {()=>{onPageChanged(p)}}>{p}</span>
           })}
      {portionCount > portionNumber && <button button = 'next' onClick={()=>{setPortionNumber(portionNumber + 1)}}>Next</button>}
      </div>  }
export default Paginator