import { NavLink } from "react-router-dom";
import standartImage from "../../../assets/images/stanI.png";
import s from "./user.module.css";
const User = ({isFollow, isAuth, unfollow, follow, user, photos }) =>{
    return  <>
       <div className={s.user} key={user.id}>
                  <div className={s.view}>
                    <div className={s.photoContainer}>
                      <NavLink to={`/profile/${user.id}`}>
                        <img alt="user's" src={user.photos.small || user.photos.large || standartImage} />
                      </NavLink>
                    </div>
                    <div className={s.buttonContainer}>
                      {user.followed ? 
                        <button disabled={isFollow.some(userId => userId === user.id) || !isAuth} onClick={() => {
                        unfollow(user.id)
                        }} className={s.button}>
                          unfollow
                        </button> : 
                        <button disabled={isFollow.some(userId => userId === user.id) || !isAuth} className={s.button} onClick={() => {
                          follow(user.id)
                        }}> 
                          follow
                        </button>}
                    </div>
                  </div>
                  <div className={s.preInfo}>
                    <div className={s.info}>
                      <div className={s.naming}>{user.name}</div>
                      <div className={s.addres}>
                      </div>
                    </div>
                    <div className={s.userStatus}>{user.status}</div>
                  </div>
                </div>
              </>
             }
export default User